body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 40px;
  font-family: Arial, sans-serif;
}

.container {
  margin-bottom: 20px;
}

.feedback-form {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  flex-direction: column;
  gap: 16px;
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  font-size: 14px;
  display: flex;
}

.feedback-form input, .feedback-form textarea {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-top: 4px;
  padding: 8px;
  font-size: 14px;
}

.feedback-form button {
  color: #fff;
  cursor: pointer;
  background: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
}

.feedback-form button:hover {
  background: #45a049;
}
